'use strict';
import './Loadmask.css';

/**
 *   .
 * @type {Object}
 */
const stat = {
	/**
	 *     Loadmask.
	 * @type {Number}
	 */
	showedAt: undefined,
	/**
	 *    Loadmask.
	 *         .
	 * ..    ,  showedAt + minTimeShow
	 * @type {Number} - 
	 */
	minTimeShow: 500,
	/**
	 *      Loadmask
	 * @type {Number}
	 */
	timerHide: undefined
};

/**
 *  " ".
 *           (, AJAX).
 */
export default class Loadmask {
	static getEl() {
		return document.getElementById('loadmask');
	}

	/**
	 *   
	 * @param text
	 */
	static show(text) {
		if (stat.timerHide) {
			clearTimeout(stat.timerHide);
		}
		stat.showedAt = Date.now();
		if (text) {
			this.setMessage(text);
		}
		this.getEl().classList.remove('hidden');
	}

	/**
	 *   .
	 */
	static hide() {
		if (Date.now() - stat.showedAt >= stat.minTimeShow) {
			this.getEl().classList.add('hidden');
			if (stat.timerHide) {
				clearTimeout(stat.timerHide);
			}
		}
		stat.timerHide = setTimeout(() => {
			this.hide();
		}, stat.minTimeShow - Date.now() - stat.showedAt);
	}

	static setMessage(text) {
		this.getEl().querySelector('.loadmask-message').innerHTML = text;
	}
};
